import React, { PureComponent } from 'react'
import Cube from './cube'

class Cubes extends PureComponent {
    render() {
        console.log(11)
        console.log(this.props.idEleMap)
        console.log(this.props.idPosMap)
        console.log(this.props.idPool)
        console.log(this.props.emptyIdPool)
        console.log(22)
        const cubes = this.props.idPool.map((id) => {
            let ele = this.props.idEleMap[id];
            if (ele === undefined || ele.id === undefined) {
                // alert(1)
                return ;
            }
            return (
                <Cube value={ele.value}
                    key={id}
                    locate={this.props.idPosMap[id]}
                    isMerge={this.props.mergedGrids[id]}
                    isNew={this.props.newGrids[id]} />
            )
        })

        return (
            <div className="cube-container">
                {cubes}
            </div>
        )
    }
}

export default Cubes;


